.homepage-container {
  width: 100%;
  display: grid;
  overflow: auto;
  min-height: 100vh;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #000000;
  margin:0;
  grid-template-rows: auto 1fr auto;
  
}
